var distro_raw_0 = [0, 0, 0, 0, 0];
var time_series_size = 365;

init_prices(0);

function init_prices(ini) {

  if (!window.net_line)
    window.net_line = "";

  //BTC, ETH, NEAR, SOL, USDC
  try {
    if (ini == 0) {
      window.price_btc = [];
      window.price_usd = [];
      window.price_eth = [];
      window.price_near = [];
      window.price_sol = [];
      window.time_all = [];
      window.rawFile = new XMLHttpRequest();
      rawFile.open("GET", "https://index.wedefin.com/stats_" + String(window.net_line) + String(0) + ".json" + "?" + String(Math.random()), false);
      rawFile.onreadystatechange = function () {
        if (rawFile.readyState === 4) {
          if (rawFile.status === 200) {
            var allText = JSON.parse(rawFile.responseText);
            for (let i = 0; i < allText['prices'].length; i++) {
              let sdate = new Date(allText['prices'][i][0]);
              window.time_all.push(String(sdate.getDate()).padStart(2, '0') + "/" + String(sdate.getMonth() + 1).padStart(2, '0') + "/" + String(sdate.getFullYear()).slice(2, 4));
              window.price_btc.push(allText['prices'][i][3]);
              window.price_usd.push(allText['prices'][i][4]);
              window.price_eth.push(allText['prices'][i][5]);
              window.price_near.push(allText['prices'][i][6]);
              window.price_sol.push(allText['prices'][i][7]);
            }
          }
        }
      }
      rawFile.send(null);
    }
  } catch { };

}

export function plot_distro_manual(ini) {

  if (!window.asset_labels)
    window.asset_labels = ["NEAR", "BTC", "ETH", "SOL", "USDC"];

  if (!window.net_line)
    window.net_line = "";

  init_prices(ini);
  distro_raw_0 = window.distro_user;

  let labels_pie_voting = [];
  let assets_pie_voting = [];
  for (let i = 0; i < window.asset_labels.length; i++) {
    labels_pie_voting.push(window.asset_labels[i].toString() + " \u2022 " + ((Math.round(distro_raw_0[i]) / 10).toFixed(1) + '%').toString());
    assets_pie_voting.push((Math.round(distro_raw_0[i]) / 10).toFixed(1));
    document.getElementById('alloc' + String(i + 1)).innerHTML = String(assets_pie_voting[i]) + '%'
  }

  let ctx_string = 'voting_0_chart_1';
  var ctx = document.getElementById(ctx_string).getContext('2d');
  let chartStatus = Chart.getChart(ctx_string);
  if (chartStatus != undefined) { chartStatus.destroy() };
  var chart = new Chart(ctx, {
    type: 'doughnut',
    //    plugins: [ChartDataLabels],
    data: {
      labels: labels_pie_voting,
      datasets: [{
        //        data: [0.8, 0.5, 1.0, 1.2],
        data: assets_pie_voting,
        //        backgroundColor: ['#E2CF56', '#E256AE', '#56E289', '#5668E2'],
        backgroundColor: ['#fc890f', '#c8251a', '#04c976', '#444244', '#2362ae'],
        borderColor: '#FEFEFE',
        borderWidth: 2,
        offset: 0,
        hoverOffset: 4,
        rotation: -20,
        spacing: 0,
        borderRadius: 6
      }]
    },
    options: {
      animation: {
        duration: 0
      },
      //        aspectRatio: 1.77,
      radius: '80%',
      cutout: '80%',
      plugins: {
        tooltip: {
          callbacks: {
            label: function (context) {
              let label = context.label;
              return label;
            }
          }
        },
        datalabels: {
          formatter: (value, ctx) => {
            let sum = 0;
            let dataArr = ctx.chart.data.datasets[0].data;
            dataArr.map(data => {
              sum += data;
            });
            let percentage = (value * 100 / sum).toFixed(1) + "%";
            return percentage;
          },
          color: '#696969',
          align: 'end',
          offset: 15,
          font: { size: "12vw" },
        },
        legend: {
          display: false,
          position: 'right',
          labels: {
            font: { size: "12vw" },
            textAlign: 'left',
            boxWidth: 10,
            boxHeight: 10,
          },
          title: {
            display: false,
            text: 'Your Proposal',
            font: { size: "14vw", weight: "normal" },
          }
        },
        title: {
          display: false,
          text: 'Your Proposal',
          padding: {
            top: 0,
            bottom: 0
          }
        }
      },
      layout: {
        padding: {
          top: 0,
          bottom: 0
        },
        autoPadding: true,
      }
    }
  });

}

export function plot_line_manual(ini) {

  if (!window.asset_labels)
    window.asset_labels = ["NEAR", "BTC", "ETH", "SOL", "USDC"];

  if (!window.net_line)
    window.net_line = "";

  init_prices(ini);
  distro_raw_0 = window.distro_user;

  let price_all_usd = [];
  let price_all_btc = [];

  let assets_pie_voting = [];
  for (let i = 0; i < window.asset_labels.length; i++) {
    assets_pie_voting.push((Math.round(distro_raw_0[i]) / 10).toFixed(1));
  }

  let price_0 = new Array(window.price_usd.length).fill(0);
  let price_1 = new Array(window.price_usd.length).fill(0);
  let price_2 = new Array(window.price_usd.length).fill(0);
  let price_3 = new Array(window.price_usd.length).fill(0);
  let price_4 = new Array(window.price_usd.length).fill(0);

  price_all_usd = new Array(window.price_usd.length).fill(0);
  let norm0 = window.price_btc[window.price_btc.length - time_series_size];
  let norm1 = window.price_usd[window.price_usd.length - time_series_size];
  let norm2 = window.price_eth[window.price_eth.length - time_series_size];
  let norm3 = window.price_near[window.price_near.length - time_series_size];
  let norm4 = window.price_sol[window.price_sol.length - time_series_size];

  for (let i = 0; i < window.price_usd.length; i++) {
    price_0[i] = (window.price_btc[i] - 0 * norm0) / norm0;
    price_1[i] = (window.price_usd[i] - 0 * norm1) / norm1;
    price_2[i] = (window.price_eth[i] - 0 * norm2) / norm2;
    price_3[i] = (window.price_near[i] - 0 * norm3) / norm3;
    price_4[i] = (window.price_sol[i] - 0 * norm4) / norm4;
  }

  //["NEAR", "BTC", "ETH", "SOL", "USDC"]
  //['MATIC', 'BTC', 'ETH', 'LINK', 'USDC']
  for (let i = 0; i < window.price_usd.length; i++) {
    price_all_usd[i] = assets_pie_voting[1] * price_0[i] + assets_pie_voting[4] * price_1[i] + assets_pie_voting[2] * price_2[i] + assets_pie_voting[0] * price_3[i] + assets_pie_voting[3] * price_4[i];
  }

  price_all_btc = new Array(window.price_usd.length).fill(0);
  norm0 = window.price_btc[window.price_btc.length - time_series_size] / window.price_btc[window.price_btc.length - time_series_size];
  norm1 = window.price_usd[window.price_btc.length - time_series_size] / window.price_btc[window.price_btc.length - time_series_size];
  norm2 = window.price_eth[window.price_btc.length - time_series_size] / window.price_btc[window.price_btc.length - time_series_size];
  norm3 = window.price_near[window.price_btc.length - time_series_size] / window.price_btc[window.price_btc.length - time_series_size];
  norm4 = window.price_sol[window.price_btc.length - time_series_size] / window.price_btc[window.price_btc.length - time_series_size];

  for (let i = 0; i < window.price_usd.length; i++) {
    price_0[i] = (window.price_btc[i] / window.price_btc[i] - 0 * norm0) / norm0;
    price_1[i] = (window.price_usd[i] / window.price_btc[i] - 0 * norm1) / norm1;
    price_2[i] = (window.price_eth[i] / window.price_btc[i] - 0 * norm2) / norm2;
    price_3[i] = (window.price_near[i] / window.price_btc[i] - 0 * norm3) / norm3;
    price_4[i] = (window.price_sol[i] / window.price_btc[i] - 0 * norm4) / norm4;
  }

  //["NEAR", "BTC", "ETH", "SOL", "USDC"]
  //['MATIC', 'BTC', 'ETH', 'LINK', 'USDC']
  for (let i = 0; i < window.price_usd.length; i++) {
    price_all_btc[i] = assets_pie_voting[1] * price_0[i] + assets_pie_voting[4] * price_1[i] + assets_pie_voting[2] * price_2[i] + assets_pie_voting[0] * price_3[i] + assets_pie_voting[3] * price_4[i];
  }

  var price_btc_norm = new Array(time_series_size).fill(0);
  var price_usd_norm = new Array(time_series_size).fill(0);
  var time_norm = new Array(time_series_size).fill(0);
  for (let i = 0; i < time_series_size; i++) {
    price_btc_norm[i] = price_all_btc[i + (price_all_btc.length - time_series_size)];
    price_usd_norm[i] = price_all_usd[i + (price_all_usd.length - time_series_size)];
    time_norm[i] = window.time_all[i + (window.time_all.length - time_series_size)];
  }

  let ctx_string = 'voting_0_chart_2';
  var ctx = document.getElementById(ctx_string).getContext('2d');
  var grad_size = 7 * document.getElementById('vote_div_0_2').offsetHeight / 10;
  var gradient1 = ctx.createLinearGradient(0, 0, 0, grad_size);
  gradient1.addColorStop(0, 'rgba(35, 98, 174, 0.9)');
  gradient1.addColorStop(1, 'rgba(35, 98, 174, 0.0)');

  var gradient2 = ctx.createLinearGradient(0, 0, 0, grad_size);
  gradient2.addColorStop(0, 'rgba(200, 37, 26, 0.9)');
  gradient2.addColorStop(1, 'rgba(200, 37, 26, 0.0)');

  let chartStatus = Chart.getChart(ctx_string);
  if (chartStatus != undefined) { chartStatus.destroy() };
  var chart2 = new Chart(ctx, {
    type: 'line',
    data: {
      labels: time_norm,
      datasets: [{
        label: 'vs USD',
        data: price_usd_norm,
        fill: true,
        backgroundColor: gradient1,
        tension: 0.1,
        borderWidth: 2,
        borderColor: '#2362ae',
        pointRadius: 0,
        yAxisID: 'y',
      },
      {
        label: 'vs BTC',
        data: price_btc_norm,
        fill: true,
        //        backgroundColor: 'rgb(226, 86, 174, 0.3)',
        backgroundColor: gradient2,
        tension: 0.1,
        borderWidth: 2,
        borderColor: '#c8251a',
        pointRadius: 0,
        yAxisID: 'y',
      }]
    },
    options: {
      maintainAspectRatio: true,
      animation: false,
      interaction: { intersect: false },
      plugins: {
        tooltip: {
          callbacks: {
            label: function (context) {
              let label = context.dataset.label || '';

              if (label) {
                label += ': ';
              }
              if (context.parsed.y !== null) {
                label += context.parsed.y.toFixed(6);
              }
              return label;
            }
          }
        },
        legend: {
          display: true,
          position: 'top',
          labels: { font: { size: "11vw" }, color: '#696969' },
          align: 'end',
          // title: { display: true, text: "Performace" }
        },
        title: {
          display: false,
          text: 'Simulated Historical Price',
          position: 'top',
          padding: {
            top: 0,
            bottom: 0
          }
        }
      },
      layout: {
        padding: {
          top: 0,
          bottom: 0,
        },
        autoPadding: true,
      },
      //      scales: { x: { type: 'time', time: { parser: "yyyy-MM-dd" }, grid: { display: false }, ticks: { font: { size: "11vw" } } } },
      scales: {
        x: {
          title: { text: "Date", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: false },
          ticks: {
            font: { size: "11vw" }, color: '#696969', maxRotation: 0, autoSkipPadding: 10,
            callback: function (value, index, values) {
              let data1 = window.time_all[value + (window.time_all.length - time_series_size)];
              return String(data1).slice(0, 5);
            },
          },
        },
        y: {
          grace: '5%',
          title: { text: "Performance (%)", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: true },
          ticks: {
            count: 6,
            font: { size: "11vw" },
            callback: function (value, index, values) {
              if (value >= 1000000000 || value <= -1000000000) {
                return value / 1e9 + 'bill';
              } else if (value >= 1000000 || value <= -1000000) {
                return value / 1e6 + 'mill';
              } else if (value >= 1000 || value <= -1000) {
                return value / 1e3 + 'k';
              }
              return (Math.round(value * 10) / 10).toFixed(1);
            },
            color: '#696969'
          }
        },
        y1: {
          grace: '5%',
          title: { text: "SER/BTC", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: false }, ticks: {
            count: 6,
            font: { size: "11vw" },
            color: '#696969',
            callback: function (value, index, values) {
              if (value >= 1000000000 || value <= -1000000000) {
                return value / 1e9 + 'bill';
              } else if (value >= 1000000 || value <= -1000000) {
                return value / 1e6 + 'mill';
              } else if (value >= 1000 || value <= -1000) {
                return value / 1e3 + 'k';
              }
              return (Math.round(value * 10) / 10).toFixed(1);
            }
          },
          position: 'right'
        }
      },
    }
  });

}


export function plot_price_manual(ini) {

  var price_btc_init = [];
  var price_usd_init = [];
  var time_init = [];
  try {
    window.rawFile = new XMLHttpRequest();
    rawFile.open("GET", "https://index.wedefin.com/wedx_price.json" + "?" + String(Math.random()), false);
    rawFile.onreadystatechange = function () {
      if (rawFile.readyState === 4) {
        if (rawFile.status === 200) {
          var allText = JSON.parse(rawFile.responseText);
          for (let i = 0; i < allText['prices'].length; i++) {
            let sdate = new Date(allText['prices'][i][0]);
            time_init.push(String(sdate.getDate()).padStart(2, '0') + "/" + String(sdate.getMonth() + 1).padStart(2, '0') + "/" + String(sdate.getFullYear()).slice(2, 4));
            price_usd_init.push(allText['prices'][i][1]);
            price_btc_init.push(allText['prices'][i][2]);
          }
        }
      }
    }
    rawFile.send(null);
  } catch { };

  if ( time_init.length == 1 ) {
    time_init.push( time_init[0] );
    price_usd_init.push( price_usd_init[0] );
    price_btc_init.push( price_btc_init[0] );
  }

  // console.log( time_init );
  // console.log( price_usd_init );
  // console.log( price_btc_init );

  if ( time_init.length > time_series_size ) {
    var price_btc_norm = new Array(time_series_size).fill(0);
    var price_usd_norm = new Array(time_series_size).fill(0);
    var time_norm = new Array(time_series_size).fill(0);
    for (let i = 0; i < time_series_size; i++) {
      price_btc_norm[i] = price_btc_init[i + (price_btc_init.length - time_series_size)];
      price_usd_norm[i] = price_usd_init[i + (price_usd_init.length - time_series_size)];
      time_norm[i] = time_init[i + (time_init.length - time_series_size)];
    }  
  } else {
    var price_btc_norm = price_btc_init;
    var price_usd_norm = price_usd_init;
    var time_norm = time_init;
  }

  let ctx_string = 'voting_0_chart_2';
  var ctx = document.getElementById(ctx_string).getContext('2d');
  var grad_size = 7 * document.getElementById('vote_div_0_2').offsetHeight / 10;
  var gradient1 = ctx.createLinearGradient(0, 0, 0, grad_size);
  gradient1.addColorStop(0, 'rgba(35, 98, 174, 0.9)');
  gradient1.addColorStop(1, 'rgba(35, 98, 174, 0.0)');

  var gradient2 = ctx.createLinearGradient(0, 0, 0, grad_size);
  gradient2.addColorStop(0, 'rgba(200, 37, 26, 0.9)');
  gradient2.addColorStop(1, 'rgba(200, 37, 26, 0.0)');

  let min_btc = Math.min(...price_btc_norm);
  let max_btc = Math.max(...price_btc_norm);
  let min_usd = Math.min(...price_usd_norm);
  let max_usd = Math.max(...price_usd_norm);

  let tick_count = 6;
  let dec_btc = -Math.floor( Math.log10( ( max_btc - min_btc ) / tick_count ) );
  let dec_usd = -Math.floor( Math.log10( ( max_usd - min_usd ) / tick_count ) );

  dec_btc = dec_btc < 0 ? 0: dec_btc;
  dec_usd = dec_usd < 0 ? 0: dec_usd;

  let chartStatus = Chart.getChart(ctx_string);
  if (chartStatus != undefined) { chartStatus.destroy() };
  var chart2 = new Chart(ctx, {
    type: 'line',
    data: {
      labels: time_norm,
      datasets: [{
        label: 'vs USD',
        data: price_usd_norm,
        fill: true,
        backgroundColor: gradient1,
        tension: 0.1,
        borderWidth: 2,
        borderColor: '#2362ae',
        pointRadius: 0,
        yAxisID: 'y',
      },
      {
        label: 'vs BTC',
        data: price_btc_norm,
        fill: true,
        //        backgroundColor: 'rgb(226, 86, 174, 0.3)',
        backgroundColor: gradient2,
        tension: 0.1,
        borderWidth: 2,
        borderColor: '#c8251a',
        pointRadius: 0,
        yAxisID: 'y1',
      }]
    },
    options: {
      maintainAspectRatio: true,
      animation: false,
      interaction: { intersect: false },
      plugins: {
        tooltip: {
          callbacks: {
            label: function (context) {
              let label = context.dataset.label || '';

              if (label) {
                label += ': ';
              }
              if (context.parsed.y !== null) {
                label += context.parsed.y.toFixed(6);
              }
              return label;
            }
          }
        },
        legend: {
          display: true,
          position: 'top',
          labels: { font: { size: "12vw" }, color: '#696969' },
          align: 'center',
          onClick: function (e, legendItem, legend) {
            const index = legendItem.datasetIndex;
            const ci = legend.chart;
            if (ci.isDatasetVisible(index)) {
              ci.hide(index);
              legendItem.hidden = true;
              if (index == 0) {
                ci.options.scales.y.ticks.color = 'white';
                ci.options.scales.y.ticks.font.size = '1vw';
                document.getElementById('label_title_usd').style.display = 'none';
              } else {
                ci.options.scales.y1.ticks.color = 'white';
                ci.options.scales.y1.ticks.font.size = '1vw';
                document.getElementById('label_title_btc').style.display = 'none';
              }
            } else {
              ci.show(index);
              if (index == 0) {
                ci.options.scales.y.ticks.color = '#696969';
                ci.options.scales.y.ticks.font.size = '11vw';
                document.getElementById('label_title_usd').style.display = 'inline-flex';
              } else {
                ci.options.scales.y1.ticks.color = '#696969';
                ci.options.scales.y1.ticks.font.size = '11vw';
                document.getElementById('label_title_btc').style.display = 'inline-flex';
              }
              legendItem.hidden = false;
            }
            ci.update();
          },
        },
        title: {
          display: false,
          text: 'Simulated Historical Price',
          position: 'top',
          padding: {
            top: 0,
            bottom: 0
          }
        }
      },
      layout: {
        padding: {
          top: 0,
          bottom: 0,
        },
        autoPadding: true,
      },
      //      scales: { x: { type: 'time', time: { parser: "yyyy-MM-dd" }, grid: { display: false }, ticks: { font: { size: "11vw" } } } },
      scales: {
        x: {
          title: { text: "Date", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: false },
          ticks: {
            font: { size: "12vw" }, color: '#696969', maxRotation: 0, autoSkipPadding: 10,
            callback: function (value, index, values) {
              let data1 = time_norm[value];
              return String(data1).slice(0, 5);
            },
          },
        },
        y: {
          grace: '5%',
          title: { text: "Price (USD)", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: true },
          ticks: {
            count: tick_count,
            font: { size: "12vw" },
            callback: function (value, index, values) {
              if (value >= 1000000000 || value <= -1000000000) {
                return value / 1e9 + 'bill';
              } else if (value >= 1000000 || value <= -1000000) {
                return value / 1e6 + 'mill';
              } else if (value >= 1000 || value <= -1000) {
                return value / 1e3 + 'k';
              }
              return (Math.round(value * 1000) / 1000).toFixed(dec_usd);
            },
            color: '#696969'
          }
        },
        y1: {
          grace: '5%',
          title: { text: "Price (BTC)", display: false, font: { size: "11vw" } },
          grid: { display: true, drawOnChartArea: false }, ticks: {
            count: tick_count,
            font: { size: "12vw" },
            color: '#696969',
            callback: function (value, index, values) {
              if (value >= 1000000000 || value <= -1000000000) {
                return value / 1e9 + 'bill';
              } else if (value >= 1000000 || value <= -1000000) {
                return value / 1e6 + 'mill';
              } else if (value >= 1000 || value <= -1000) {
                return value / 1e3 + 'k';
              }
              return (Math.round(value * 100000000) / 100000000).toFixed(dec_btc);
            }
          },
          position: 'right'
        }
      },
    }
  });

}

